.conditions {
  margin-top: 120px;

  @include vp-1199 {
    margin-top: 100px;
  }
}

.conditions__wrapper {
  padding-top: 120px;
  padding-bottom: 120px;
  background-color: $color-cerulean-blue;
  color: $color-default-white;
  display: grid;
  grid-template-columns: 498px 498px;
  column-gap: 138px;

  @include vp-1199 {
    padding-top: 100px;
    padding-bottom: 100px;
    display: flex;
    flex-direction: column;
  }
}

.conditions__main {
  h2 {
    font-size: 50px;
    font-weight: 400;
    line-height: 50px;
    margin: 0;

    @include vp-1199 {
      font-size: 34px;
      line-height: 50px;
    }
  }

  p {
    margin: 0;
    margin-top: 42px;
    margin-bottom: 40px;
    font-size: 16px;
    font-weight: 400;
    line-height: 26px;

    @include vp-1199 {
      line-height: 20px;
      margin-top: 30px;
      margin-bottom: 40px;
    }
  }

  img {
    border-radius: 16px;

    @include vp-1199 {
      width: 100%;
    }
  }
}

.conditions__info {
  @include vp-1199 {
    margin-top: 40px;
  }

  h3 {
    margin: 0;
    font-size: 32px;
    font-weight: 400;
    line-height: 36px;

    @include vp-1199 {
      font-size: 28px;
      line-height: 28px;
    }
  }
}

.conditions__list {
  display: flex;
  flex-direction: column;
  gap: 23px;
  padding: 0;
  margin: 0;
  margin-top: 61px;
  list-style: none;
  padding-left: 20px;
  counter-reset: conditions;

  @include vp-1199 {
    margin-top: 35px;
    display: grid;
    grid-template-columns: 1fr 1fr;
  }

  li {
    position: relative;
    padding: 25px;
    padding-right: 62px;
    font-size: 16px;
    font-weight: 400;
    line-height: 26px;
    border-radius: 16px;
    border: 1px solid $color-default-white;

    @include vp-1199 {
      padding: 30px 25px;
      padding-right: 27px;
    }
  }
}

.conditions__list li {
  &::after {
    counter-increment: conditions;
    content: counter(conditions);
    position: absolute;
    color: $color-default-white;
    width: 42px;
    height: 42px;
    position: absolute;
    background-color: $color-cerulean-blue;
    display: flex;
    align-items: center;
    justify-content: center;
    top: -21px;
    left: -21px;
    font-weight: 500;
    line-height: 22px;
    letter-spacing: 0.22px;
    text-transform: uppercase;

    @include vp-1199 {
      font-size: 22px;
      top: -14px;
    }
  }
}
